import BASE_HOST from '../../../util/util.js'
export default {
	data() {
		return {
			title: 'Hello',
			local_myinfo: {},
			password: '',
			verify_password: ''
		}
	},
	onShow() {
		this.initMyInfo()
	},
	onLoad() {
		
	},
	methods: {
		initMyInfo() {
			this.local_myinfo = uni.getStorageSync('local_userinfo')
		},
		save() {
			var _this = this
			if(this.password.length < 6) {
				uni.showToast({
					title: '密码太短了',
					duration: 2000,
					icon: 'none'
				});
				return
			}
			
			if(this.password === this.verify_password) {
				 uni.request({
				 	url: BASE_HOST + 'user/resetPwd',
					method:'POST',
					data:{
						'id': _this.local_myinfo.token,
						'userPassword': _this.$md5(_this.password)
					},
					success(res) {
						if(res.data.code !== 20000) {
							uni.showToast({
								title: res.data.msg,
								duration: 2000,
								icon: 'none'
							});
						} else {
							uni.setStorage({
							    key: 'local_userinfo',
							    data: ''
							});
							uni.switchTab({
								url: '../login/login'
							})
						}
					}
				 })
			} else {
				uni.showToast({
					title: '密码不一致',
					duration: 2000,
					icon: 'none'
				});
			}
		
		},
		reback() {
			uni.switchTab({
				url: '../me/me'
			})
		}
	}
}
